import React, { Component } from 'react';
import { Col, Card, Row, Form, Popconfirm, Checkbox, message, Input } from 'antd';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import {
  getPaperStudent,
  // getPapeType,
  savePaperType,
  deletePaper,
} from 'services/AdditionalAdmissionPapersPage';
import { BtnDelete, BtnSave, BtnCancel } from 'components/common/button';
import { TableData } from 'components/common';
import { dateToString } from '../../../utils/formatUtil';

@Form.create({ name: 'FormSearchStudent' })
class PaperStudentDetail extends Component {
  state = {
    reload: 0,
    initialValue: {},
  };

  deletePaper = async record => {
    const { reload } = this.state;
    await deletePaper(this.props.match.params.id, record.paperTypeId);
    message.success('Xo thnh cng');
    this.setState({
      reload: reload + 1,
    });
  };

  savePaperType = async () => {
    const { reload } = this.state;
    await this.props.form.validateFields(async (err, values) => {
      await savePaperType(this.props.match.params.id, values.paperId);
      message.success('Thm giy t thnh cng');
      this.setState({
        reload: reload + 1,
      });
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { reload } = this.state;
    const { initialValue } = this.state;
    // const paperId = getFieldValue('paperId');
    const columns = [
      {
        title: 'M giy t',
        dataIndex: 'paperTypeId',
        key: 'paperTypeId',
      },
      {
        title: 'Tn giy t',
        dataIndex: 'paperTypeLabel',
        key: 'paperTypeLabel',
      },
      {
        title: 'Ngy nhn',
        dataIndex: 'receivedDate',
        key: 'receivedDate',
        render: (text, record) => <div>{dateToString(record.receivedDate)}</div>,
      },
      {
        title: 'Thao tc',
        render: (text, record) => (
          <Popconfirm
            title="ng  xa ?"
            onConfirm={() => this.deletePaper(record)}
            okText="Xa"
            cancelText="Hy"
          >
            <BtnDelete />
          </Popconfirm>
        ),
      },
    ];
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card title="Thng tin giy t">
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item hasFeedback label="M giy t" {...formItemLayout}>
              {getFieldDecorator('trainTypeId', {
                initialValue: initialValue.trainTypeId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Tn giy t" {...formItemLayout}>
              {getFieldDecorator('rrr', {
                initialValue: initialValue.courseId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="S lng" {...formItemLayout}>
              {getFieldDecorator('paperTypeId', {
                initialValue: initialValue.paperTypeId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="S lng  np" hasFeedback>
              {getFieldDecorator('noOfPieces', {
                initialValue: initialValue.noOfPieces,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Ngy np" hasFeedback>
              {getFieldDecorator('note', {
                initialValue: initialValue.note,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Giy t np ">
              {getFieldDecorator('isMandantory', {
                initialValue: initialValue.isMandantory,
                valuePropName: 'checked',
              })(<Checkbox />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getPaperStudent}
              paramSearch={{ studentId: this.props.match.params.id }}
              columns={columns}
              rowKey="paperTypeId"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <PaperStudentDetail {...props} />
  </Page>
));



